import React, { Component, useState } from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import Form from '../../../../components/pages-component/form/form'
import FormControl from '../../../../components/pages-component/form-controls/components/form-control';
import './create-blog.css'
import '../../../../components/pages-component/button/button.css'
import Button from '../../../../components/pages-component/button/button'
import uuid from 'uuid/';
import createBlog from '../../../../services/api/create-blog'
import ErrorBox from '../../../../components/pages-component/form/components/error-box';
import Loader from '../../../../components/ui/loader/loader';
import * as actionType from '../../../../store/actions/action-type'

import Header from '../../../../components/layout/header/header';
import Footer from '../../../../components/layout/footer/footer';


const CreateBlog = (props) => {

    const [id, setId] = useState(uuid());
    const [author, setAuthor] = useState('');
    const [title, setTitle] = useState('');
    const [thumbnail, setThumbnail] = useState('');
    const [publish, setPublish] = useState(false);
    const [editContent, setEditContent] = useState(false);
    const [formSubmissionStart, setFormSubmissionStart] = useState(false);
    const [readOnly, setReadOnly] = useState(false);
    const [errorMsg, setErrorMsg] = useState(null);
    const [btnDisable, setBtnDisable] = useState(false);


    const handleInputChange = (e) => {
        e.target.name(e.target.value)
        const target = e.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        name(value);
    }

    const submitCallback = (data) => {
        const blogData = createBlogData();
        if (data.status === 201) {
            props.addBlogItems(blogData)
            setEditContent(true)
            setReadOnly(true)
            setErrorMsg(null)
            setFormSubmissionStart(false)
        } else if (data.response) {
            setErrorMsg(data.response.data)
            setFormSubmissionStart(false)
            setEditContent(false)
            setReadOnly(false)
            setBtnDisable(false)

        } else {
            const genericErrorMsg = { Error: ["Oops! Something went wrong, please try again."] };
            setErrorMsg(genericErrorMsg)
            setFormSubmissionStart(false)
            setEditContent(false)
            setReadOnly(false)
            setBtnDisable(false)
        }
    }

    const onSubmitHandle = (e) => {
        e.preventDefault()
        setFormSubmissionStart(true)
        setBtnDisable(true)
        const data = createBlogData();
        if (data) {
            createBlog(submitCallback, data)
        }
    }

    const createBlogData = () => {
        const data = {
            id: id,
            author: author,
            title: title,
            thumbnail: thumbnail,
            publish: publish
        }
        return data
    }

    const clearForm = () => {
        setAuthor('')
        setTitle('')
        setThumbnail('')
        setPublish(false)
    }

    return (
        <div className="page page--blogs-page">
            <Header />
            <section className="section section--blogs">
                <div className="container">
                    <div className="create-blog-form">
                        <Form classValue="form" onSubmitHandler={onSubmitHandle}>
                            <FormControl>
                                <label htmlFor="title">
                                    Title
                                        <span className="star"> *</span>
                                </label>
                                <input
                                    type="text"
                                    className="input-element"
                                    placeholder="Enter Blog's Title"
                                    name="title"
                                    id="title"
                                    required={true}
                                    readOnly={readOnly}
                                    value={title}
                                    onChange={e => handleInputChange(e)}
                                />
                            </FormControl>
                            <FormControl>
                                <label htmlFor="author">
                                    Author
                                        <span className="star"> *</span>
                                </label>
                                <input
                                    type="text"
                                    className="input-element"
                                    placeholder="Enter Author Name"
                                    name="author"
                                    id="author"
                                    required={true}
                                    readOnly={readOnly}
                                    value={author}
                                    onChange={e => handleInputChange(e)}
                                />
                            </FormControl>
                            <FormControl>
                                <label htmlFor="thumbnail">
                                    Thumbnail
                                        <span className="star"> *</span>
                                </label>
                                <input
                                    type="text"
                                    className="input-element"
                                    placeholder="Enter Image URL"
                                    name="thumbnail"
                                    id="thumbnail"
                                    required={true}
                                    readOnly={readOnly}
                                    value={thumbnail}
                                    onChange={e => handleInputChange(e)}
                                />
                            </FormControl>
                            <div style={{ margin: 5 }}>
                                <input
                                    type="checkbox"
                                    name="publish"
                                    id="publish"
                                    onChange={e => handleInputChange(e)}
                                    checked={publish}
                                />{" "}<span><b>Publish</b></span>
                            </div>
                            {errorMsg ? <ErrorBox isRequired errorMsg={errorMsg} /> : null}
                            {formSubmissionStart ? <Loader>Submitting your request...</Loader> : null}
                            <Button
                                type="submit"
                                classValue="form-btn"
                                disabled={btnDisable}
                            >
                                Create
                                </Button>
                        </Form>
                        {
                            editContent
                                ?
                                <Link to={`/blogs/create/content/${id}`}>
                                    <button className="form-btn" >Edit Content</button>
                                </Link>
                                : null
                        }
                    </div>
                </div>
            </section>
            <Footer />
        </div>
    )
}


const mapDispatchToProps = (dispatch) => {
    return {
        addBlogItems: (data) => {
            dispatch({
                type: actionType.ADD_BLOG_ITEM,
                value: data
            })
        },

    }
}

export default connect(null, mapDispatchToProps)(CreateBlog)